<script lang="ts" setup>
import {ref} from 'vue'
import {message} from 'ant-design-vue'
import type {VxeGridListeners} from 'vxe-table'
import {destroy} from "@/api/couponController";
import useCoupon from "@/composible/useCoupon";

const {gridOptions} = useCoupon()
const xGrid = ref()

// 编辑用户
const editCourse = (row: any) => {
  const grid = xGrid.value
  grid.setEditRow(row)
}

// 删除用户
const delCourse = async (row: any) => {
  try {
    await destroy({id: row.id})
    xGrid.value.commitProxy('query')
  } catch (error) {
    message.error('删除失败')
  }
}


const gridEvent: VxeGridListeners = {
  proxyQuery() {
    /*设置选项*/
    const column = xGrid.value.getColumnByField('course_type_id')
  },
}

/*"created", "paid", "used", "expired"*/
const formatStatus = (status) => {
  switch (status) {
    case "created":
      return "已创建";
    case "paid":
      return "已支付";
    case "used":
      return "已使用";
    case "expired":
      return "已过期";
    default:
      return "未知";
  }
}
</script>

<template>
  <div class="demo-page-wrapper">
    <vxe-grid
        ref="xGrid"
        v-bind="gridOptions"
        v-on="gridEvent"
    >
      <template #status="{ row }">
        <div>
          <a-tag color="green" v-if="row.status==='paid'">{{formatStatus(row.status)}}
          </a-tag>
          <a-tag color="error" v-if="row.status==='used'">{{formatStatus(row.status)}}
          </a-tag>
          <a-tag color="default" v-if="row.status==='expired'">{{formatStatus(row.status)}}
          </a-tag>
        </div>
      </template>
      <template #action="{ row }">
        <a-space>
          <a-popconfirm
              title="确认退款吗？"
              ok-text="是"
              cancel-text="否"
              @confirm="refundCourse(row)"
          >
            <a-button size="small" type="primary" danger>退款</a-button>
          </a-popconfirm>
        </a-space>
      </template>
    </vxe-grid>
  </div>
</template>

<style scoped lang="less">
.demo-page-wrapper {
  padding: 20px;
}
</style>